/* Work Sans font */
@import url("https://fonts.googleapis.com/css2?family=Work+Sans:wght@300;400;500;600;700&display=swap");

/* ===== GLOBAL TEXT SIZE (allt lite mindre) ===== */
html {
  font-size: 14px;
}

body,
html,
#root {
  margin: 0;
  padding: 0;
}

/* ===== BAKGRUND ===== */
body {
  font-family: "Work Sans", system-ui, -apple-system, BlinkMacSystemFont,
    sans-serif;
}

.page-background {
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* ===== LAYOUT ===== */

.main-columns {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 2rem;
}

.left-column,
.right-column {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

@media (min-width: 900px) {
  .main-columns {
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
  }

  .left-column,
  .right-column {
    flex: 1;
    max-width: 600px;
  }
}

/* ===== DOS-FÖNSTER ===== */

.window {
  background-color: #d3d3d3;
  border: 2px solid #000;
  box-shadow: 4px 4px #000;
}

.title-bar {
  background: linear-gradient(to right, #000080, #0000cd);
  color: white;
  padding: 4px 8px;
  font-weight: bold;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
}

.title-bar-buttons span {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-color: #c0c0c0;
  margin-left: 4px;
  border: 1px solid #000;
  text-align: center;
  font-size: 12px;
  line-height: 16px;
  font-weight: bold;
  cursor: pointer;
}

.content {
  padding: 1rem;
  background-color: #e0e0e0;
  border: 2px inset #fff;
}

/* ========================================= */
/*         SPRINGANDE GUBBAR-BANNER          */
/* ========================================= */

/* Mindre och lägre banner */
.banner-track {
  background: linear-gradient(to right, #000080, #0000cd);
  padding: 0;
  height: 16px;
  overflow: hidden;
  display: flex;
  align-items: center;
}

.banner-marquee.single {
  display: inline-flex;
  align-items: flex-end;    /* Gubbarna springer längst fram */
  gap: 6px;
  animation: banner-slide-single 10s linear infinite;
  height: 16px;             /* samma höjd som banner-track */
}


/* Animation höger → vänster */
@keyframes banner-slide-single {
  0% {
    transform: translateX(120%);
  }
  100% {
    transform: translateX(-120%);
  }
}

.runner-emoji {
  font-size: 0.9rem;              /* passande för 16px banner */
  line-height: 1;                 /* ingen vertikal centreringsbugg */
  animation: runner-legs 0.35s steps(2) infinite;
}

/* Falsk springeffekt */
@keyframes runner-legs {
  0% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(-3px) rotate(-6deg);
  }
  100% {
    transform: translateY(0px) rotate(0deg);
  }
}

/* Vit banderoll */
.banner-flag {
  background-color: #ffffff;
  padding: 0px 8px;
  font-size: 0.65rem;
  border-radius: 3px;
  box-shadow: 1px 1px 0 #00000066;
  white-space: nowrap;
  line-height: 1;
  display: flex;
  align-items: center;
  height: 14px;              /* passar in i 16px-banner */
}


/* ========================================= */
/*                PAKETRUTA                  */
/* ========================================= */

.paket {
  background-color: #ffffff;
  border: 2px dashed #0000cd;
  padding: 1rem;
  box-shadow: 2px 2px #808080;
}

.paket ul {
  list-style: none;
  padding-left: 0;
}

.paket li {
  margin-bottom: 0.3rem;
}

/* ========================================= */
/*                  BANGER                   */
/* ========================================= */

.banger h2 {
  margin-top: 0;
}

.banger iframe {
  border: 2px inset #000;
  height: 80px !important;
}

/* ========================================= */
/*                OM OSS BILDER              */
/* ========================================= */

.omoss-bilder {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
  justify-content: center;
}

.dos-image-frame {
  border: 2px solid black;
  padding: 4px;
  background-color: #c0c0c0;
  text-align: center;
  font-family: "Courier New", monospace;
  font-size: 12px;
}

.dos-image-frame img {
  width: 120px;
  height: auto;
  display: block;
}

/* ========================================= */
/*            KONTAKT-FORMULÄR                */
/* ========================================= */

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}

.contact-form input,
.contact-form textarea {
  font-family: inherit;
  padding: 4px;
  border: 1px solid #555;
  background-color: #ffffff;
}

.contact-form fieldset {
  border: 1px solid black;
  padding: 0.5rem;
  margin-top: 0.8rem;
}

.contact-form legend {
  padding: 0 4px;
  font-weight: 600;
  font-size: 14px;
}

.contact-form label {
  font-size: 14px;
}

.contact-form label input[type="checkbox"] {
  margin-right: 4px;
}

.send-button {
  font-size: 16px;
  padding: 10px 22px;
  background-color: #e6b11d;
  color: black;
  border: 3px outset #a37800;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: bold;
  font-family: "Work Sans", sans-serif;
}

.send-button:hover {
  background-color: #cc9900;
  border: 3px inset #a37800;
}

/* ========================================= */
/*                   FOOTER                  */
/* ========================================= */

.footer {
  text-align: center;
  padding: 1rem 0 1.5rem;
  font-size: 12px;
  font-family: monospace;
  color: white;
}
